import * as React from "react";
import Button from "@mui/material/Button";
import Dialog from "@mui/material/Dialog";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogContentText from "@mui/material/DialogContentText";
import DialogTitle from "@mui/material/DialogTitle";

export const ModalTerminosCondiciones = () => {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button
        variant="outlined"
        fullWidth
        onClick={handleClickOpen}
        sx={{ mt: 0.9 }}
      >
        Leer Trminos y Condiciones
      </Button>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogTitle id="alert-dialog-title" sx={{ mt: 1 }}>
          Trminos y Condiciones New Hopes
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            Bienvenido a New Hopes proporcionado por Ignacio Mansilla. Nos
            complace ofrecerle acceso al Servicio (como se define ms abajo),
            sujeto a estos trminos y condiciones (los "Trminos de Servicio") y
            a la Poltica de Privacidad correspondiente de New Hopes. Al acceder
            y utilizar el Servicio, usted expresa su consentimiento, acuerdo y
            entendimiento de los Trminos de Servicio y la Poltica de
            Privacidad. Si no est de acuerdo con los Trminos de Servicio o la
            Poltica de Privacidad, no utilice el Servicio. Si utiliza el
            servicio est aceptando las modalidades operativas en vigencia
            descriptas ms adelante, las declara conocer y aceptar, las que se
            habiliten en el futuro y en los trminos y condiciones que a
            continuacin se detallan: <br />
            <b>Privacidad de la informacin</b> Para utilizar los Servicios
            ofrecidos por New Hopes, los Usuarios debern facilitar determinados
            datos de carcter personal. Su informacin personal se procesa y
            almacena en servidores o medios magnticos que mantienen altos
            estndares de seguridad y proteccin tanto fsica como tecnolgica.
            Para mayor informacin sobre la privacidad de los Datos Personales y
            casos en los que ser revelada la informacin personal, se pueden
            consultar nuestras polticas de privacidad.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} autoFocus>
            Cerrar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};
